.scenery {
	width: 100%;
	height: 50%;
	position: absolute;
	bottom: 0;
	left: 0;
}

img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

#foreground1,
#foreground2 {
	z-index: 1;
}

#foregeound2,
#background2 {
	transform: translateX(100%);
}

