.earth {
	width: 100%;
	height: 50vh;
	left: 0;
	position: absolute;
	background: #eb125d
		url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/641/bg_earth.jpg")
		repeat-x 0 0 / 100% auto;
	bottom: 0;
}

.alice {
	position: absolute;
	top: 0;
	left: 40%;
	transform: translate(-50%, -50%);
	overflow: hidden;
	width: 80%;
	max-width: 450px;
	z-index: 1;
}

.alice:before {
	content: " ";
	display: block;
	padding-top: 87%;
}

.alice img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}
